@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
:root {
  --accent-color: hsl(264deg 100% 64%);
  --main-bg: hsl(240deg 7% 6%);
  --main-text-color: hsl(240deg 7% 94%);
  --main-control-bg: hsl(240deg 3% 23%);
  --main-control-color: hsl(0deg 0% 81%);
  --main-control-pressed: hsl(240 4% 19%);
  --main-control-hover: hsl(0deg 0% 19%);
  --main-success-color: hsl(122deg 39% 49%);
  --main-danger-color: hsl(1deg 77% 55%);
  --secondary-bg: hsl(240deg 6% 10%);
  --secondary-bg-hover: hsl(240deg 6% 14%);
  --secondary-sub-bg: hsl(240deg 2% 20%);
  --secondary-color: hsl(213deg 5% 58%);
  --secondary-sub-color: hsl(240deg 7% 68%);
  --secondary-color-light: hsl(240deg 4% 34%);
  --secondary-tint: hsl(240deg 6% 13%);
  --selection-bg: hsl(264deg 80% 54%);
  --selection-color: hsl(60deg 2% 84%);
  --dark-monochrome: hsl(0deg 0% 100%);
  --light-monochrome: hsl(0deg 0% 100%);
  --default-transparent: hsl(0deg 0% 0% / 0%);
  --default-transparent-01: rgb(0 0 0 / 10%);
  --default-transparent-02: rgb(0 0 0 / 20%);
  --default-transparent-06: rgb(0 0 0 / 60%);
  --default-transparent-08: rgb(0 0 0 / 80%);
  --secondary-transparent-01: rgb(173 173 184 / 10%);
  --secondary-transparent-02: rgb(173 173 184 / 20%);
  --secondary-transparent-04: rgb(173 173 184 / 40%);
}
.light-theme {
  --main-bg: hsl(0deg 0% 88%);
  --main-text-color: hsl(0deg 0% 0%);
  --main-control-bg: hsl(0deg 0% 91%);
  --main-control-color: hsl(0deg 0% 38%);
  --main-control-pressed: hsl(0deg 0% 82%);
  --main-control-hover: hsl(0deg 0% 78%);
  --secondary-bg: hsl(0deg 0% 100%);
  --secondary-bg-hover: hsl(0deg 0% 78%);
  --secondary-sub-bg: hsl(0deg 0% 84%);
  --secondary-color: hsl(0deg 0% 40%);
  --secondary-sub-color: hsl(0deg 0% 30%);
  --secondary-color-light: hsl(0deg 0% 62%);
  --secondary-tint: hsl(0deg 0% 76%);
  --dark-monochrome: hsl(0deg 0% 0%);
  --secondary-transparent-01: rgb(0 0 0 / 10%);
  --secondary-transparent-02: rgb(255 255 255 / 20%);
  --secondary-transparent-04: rgb(18 18 18 / 40%);
}
* {
  outline: none; 
  box-sizing: border-box;
  text-decoration: none;
  margin: 0;
  padding: 0;
  transition: background .2s ease-in;
}
::selection {
  color: hsl(60deg 2% 84%);
  color: var(--selection-color);
  background-color: hsl(264deg 80% 54%);
  background-color: var(--selection-bg);
  text-shadow: none;
}
html, body {
  height: 100%;
}
body {
  font-family: 'Roobert', 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.4;
  margin: 0 auto;
  color: hsl(240deg 7% 94%);
