.user-profile-content .user-head {
  width: 100%;
  overflow: hidden;
  background-color: rgb(0 0 0 / 60%);
  background-color: var(--default-transparent-06);
  position: relative;
  padding: 50px 0 24px;
}

.user-profile-content .user-head::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  /*filter: blur(2px);*/
  z-index: -1;
}

.user-profile-content .user-head::after {
  content: '';
  display: block;
  position: absolute;
  left: -40px;
  right: -40px;
  height: 16rem;
  background-color: hsl(240deg 6% 10%);
  background-color: var(--secondary-bg);
  transform: rotate(2deg);
  transition: background .2s ease-in;
  z-index: 1;
}

.user-profile-content .user-head div.user-info {
  display: flex;
  padding: 32px 16px 64px;
  position: relative;
  align-items: center;
  justify-content: center;
  z-index: 9;
}

